@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

/* Forgot PIN Section */
.forgot-pin-section {
  margin-top: 30px;
  text-align: center;
}

.forgot-pin-link {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  cursor: pointer;
  text-decoration: underline;
  transition: color 0.3s ease;
  padding: 0;
}

.forgot-pin-link:hover {
  color: rgba(255, 255, 255, 0.9);
}

/* Demo Credentials */
.demo-credentials {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  animation: slideInRight 0.3s ease-out;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.demo-info-card {
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 20px;
  min-width: 250px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.demo-info-card p {
  color: rgba(255, 255, 255, 0.9);
  margin: 0 0 15px 0;
  font-weight: 600;
}

.demo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
}

.demo-item code {
  background: rgba(255, 255, 255, 0.1);
  color: #4ecdc4;
  padding: 4px 8px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  font-size: 12px;
  color: #adb5bd;
}

/* Dashboard Styles */
.dashboard {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 100px);
}

.dashboard-header {
  text-align: center;
  margin-bottom: 3rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.dashboard-header h1 {
  font-size: 3rem;
  font-weight: 700;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.dashboard-subtitle {
  font-size: 1.2rem;
  color: #718096;
  margin: 0;
  font-weight: 500;
}

.dashboard-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.welcome-section {
  text-align: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.welcome-section h2 {
  font-size: 2rem;
  color: #2d3748;
  margin: 0 0 1rem 0;
  font-weight: 600;
}

.welcome-section p {
  font-size: 1.1rem;
  color: #718096;
  margin: 0;
}

.dashboard-actions {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.action-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.action-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.action-card h3 {
  font-size: 1.5rem;
  color: #2d3748;
  margin: 0 0 1rem 0;
  font-weight: 600;
}

.action-card p {
  color: #718096;
  margin: 0 0 1.5rem 0;
  font-size: 1rem;
  line-height: 1.6;
}

.dashboard-btn {
  width: 100%;
  padding: 1rem 2rem;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.dashboard-btn.primary {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.dashboard-btn.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.dashboard-btn.secondary {
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  color: white;
}

.dashboard-btn.secondary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(79, 172, 254, 0.4);
}

@media (max-width: 768px) {
  .dashboard {
    padding: 1rem;
  }
  
  .dashboard-header h1 {
    font-size: 2rem;
  }
  
  .dashboard-actions {
    grid-template-columns: 1fr;
  }
  
  .action-card {
    padding: 1.5rem;
  }
}

.use-demo-btn {
  width: 100%;
  background: linear-gradient(135deg, #4ecdc4 0%, #44a08d 100%);
  color: white;
  border: none;
  padding: 10px;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 15px;
}

.use-demo-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(78, 205, 196, 0.3);
}

/* Loading Overlay */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  z-index: 1000;
}

.loading-spinner-elegant {
  font-size: 32px;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.1); opacity: 0.7; }
}

.loading-text {
  color: rgba(255, 255, 255, 0.9);
  margin-top: 15px;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.5px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .elegant-login-content {
    padding: 40px 20px;
    min-width: auto;
    width: 100%;
    max-width: 400px;
  }

  .avatar-circle {
    width: 100px;
    height: 100px;
  }

  .hotel-icon {
    font-size: 40px;
  }

  .user-display-name {
    font-size: 20px;
  }

  .demo-credentials {
    position: static;
    margin-top: 20px;
    width: 100%;
  }

  .demo-info-card {
    width: 100%;
    min-width: auto;
  }
